import {
  Streamlit,
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { ReactNode } from "react";
import Slider from '@material-ui/core/Slider';

interface State {
  lat: number,
  long: number,
  x: number,
  y: number,
  w: number,
  h: number
}

/**
 * This is a React-based component template. The `render()` function is called
 * automatically when your component should be re-rendered.
 */
class NebulaMap extends StreamlitComponentBase<State> {
  public state = { lat: 0, long: 0, x: 0, y: 0, w:0, h:0 }
  public initial_lat: number = 0;
  public initial_long: number = 0;

  private handleLatChange = (event:any, newValue:any) => {
    this.changeLat(newValue);
  };

  private handleLongChange = (event:any, newValue:any) => {
    this.changeLong(newValue);
  };
  
  public render = (): ReactNode => {
  
    this.initial_lat = this.props.args["lat"];
    this.initial_long = this.props.args["long"];
    
    return (
      <div>
      <div>
      
        {/* ================================ 
          MAP
          ================================*/}
        <img src={require('./mapScan2.jpg')} alt="mapScan" 
        style={{ width: "100%" }}
        onClick={this.getMousePosition.bind(this)}
        onLoad={this.setCoord.bind(this)}/>
        
        {/* ================================ 
          SATELLITE
          ================================*/}
        <img src={require('./satellite.png')} alt="sat" 
        style={{ zIndex: 99, 
                 position: 'absolute', 
                 top:(this.state.y - (this.state.w*0.17*0.77)), 
                 left:(this.state.x - (this.state.w*0.17*0.74)), 
                 width: "17%" }}
        onClick={this.getSatMousePosition.bind(this)}/>

      </div>
      
      <div>
      
      {/* ================================ 
          DISPLAY LATITUDE SLIDER
          ================================*/}

        <div style={{display: 'inline-block', width:"30%", fontSize: "small", verticalAlign:"top"}}>
           Lat : {Math.round(this.state.lat * 100) / 100}°
        </div>
        <div style={{display: 'inline-block', width: "70%"}}>
        <Slider value={this.state.lat} aria-labelledby="continuous-slider" color="secondary"
                          onChange={this.handleLatChange}
                  min={-69.11} max={52.43} step={0.01}/>
        </div>
       </div>
      
      {/* ================================ 
          DISPLAY LONGITUDE SLIDER
          ================================*/}
      <div>
        <div style={{display: 'inline-block', width:"30%", fontSize: "small", verticalAlign:"top"}}>
           Long : {Math.round(this.state.long * 100) / 100}°
        </div>
        <div style={{display: 'inline-block', width: "70%"}}>
        <Slider value={this.state.long} aria-labelledby="continuous-slider" color="secondary" 
                          onChange={this.handleLongChange}
                  min={-129.26} max={54.35} step={0.01}/>
        </div>
       </div>
      </div>
    )
  }

  private changeLat(newLat: any) {
    const roundedLat = Math.round(newLat * 100) / 100;
    const width = this.state.w,
          height = this.state.h,
          x = this.state.x,
          y = height/100 * ((-roundedLat/1.35)+45),
          lat = roundedLat,
          long = this.state.long;
    this.setState({ lat: lat, long: long, x: x, y: y, w: width, h:height },
     () => Streamlit.setComponentValue([this.state.lat, this.state.long, this.state.x, this.state.y, this.state.w , this.state.h]));
  }

  private changeLong(newLong: any) {
    const roundedLong = Math.round(newLong * 100) / 100;
    const width = this.state.w,
          height = this.state.h,
          x = width/100 * ((roundedLong/2)+69),
          y = this.state.y,
          lat = this.state.lat,
          long = roundedLong;
    this.setState({ lat: lat, long: long, x: x, y: y, w: width, h:height },
     () => Streamlit.setComponentValue([this.state.lat, this.state.long, this.state.x, this.state.y, this.state.w , this.state.h]));
  }
  
  private getMousePosition(e: any) {
    let currentTargetRect = e.currentTarget.getBoundingClientRect();
    const event_offsetX = e.pageX - currentTargetRect.left,
          event_offsetY = e.pageY - currentTargetRect.top,
          width = currentTargetRect.width,
          height = currentTargetRect.height,
          lat = (45 - (event_offsetY*100/height)) * 1.35,
          long = -(69 - (event_offsetX*100/width)) * 2;
    const roundedLat = Math.round(lat * 100) / 100;
    const roundedLong = Math.round(long * 100) / 100;
    this.setState({ lat: roundedLat, long: roundedLong, x: event_offsetX, y: event_offsetY, w: width, h:height },
     () => Streamlit.setComponentValue([this.state.lat, this.state.long, this.state.x, this.state.y, this.state.w , this.state.h]));
  }

  private getSatMousePosition(e: any) {
    let currentTargetRect = e.currentTarget.parentNode.getBoundingClientRect();
    const event_offsetX = e.pageX - currentTargetRect.left,
          event_offsetY = e.pageY - currentTargetRect.top,
          lat = (45 - (event_offsetY*100/this.state.h)) * 1.35,
          long = -(69 - (event_offsetX*100/this.state.w)) * 2;
    const roundedLat = Math.round(lat * 100) / 100;
    const roundedLong = Math.round(long * 100) / 100;
    this.setState({ lat: roundedLat, long: roundedLong, x: event_offsetX, y: event_offsetY, w: this.state.w, h:this.state.h },
     () => Streamlit.setComponentValue([this.state.lat, this.state.long, this.state.x, this.state.y, this.state.w , this.state.h]));
  }
  
  private setCoord(e: any) {
    let boundingClientRect = e.currentTarget.getBoundingClientRect();
    const width = boundingClientRect.width,
          height = boundingClientRect.height,
          x = width/100 * ((this.initial_long/2)+69),
          y = height/100 * ((-this.initial_lat/1.35)+45),
          lat = this.initial_lat,
          long = this.initial_long;
    this.setState({ lat: lat, long: long, x: x, y: y, w: width, h:height },
     () => Streamlit.setComponentValue([this.state.lat, this.state.long, this.state.x, this.state.y, this.state.w , this.state.h]));

  }  

}

// "withStreamlitConnection" is a wrapper function. It bootstraps the
// connection between your component and the Streamlit app, and handles
// passing arguments from Python -> Component.
//
// You don't need to edit withStreamlitConnection (but you're welcome to!).
export default withStreamlitConnection(NebulaMap)
